/*define styles here*/
.info-link{
  color: #17a2b8;
  &:hover{
    color: #8cc8ca;
  }    
}
/* input */
#searchtext{
  border-radius: 0!important;
}
.btn a {
  color: inherit;
  text-decoration: none;
  width: 100%;
  &:hover{
    color: inherit;
    text-decoration: none;
  }    
}
/*NAVBAR*/
.navbar-profile {
  display: inline-flex;
  position: absolute;
  right: 2%;
}
.user-avatar {
  width: 25px;
  height: 25px;
  display: inline-block;
  vertical-align: top;
}
.user-username {
  margin-left: 10px;
}
/*FORMS*/
.form-control {
  border-radius: 0;
}
.form-group input, .form-group div, .form-group button {
  border-radius: 0;
}
/*MODAL CLOSE*/
.close span {
  color: #fff;
}

/* TABLES */
.table th, .table td, .table thead th {
    vertical-align: inherit!important;
    flex-wrap: wrap!important;
    word-break: break-all!important;
}
.table-userprofile td {
  width: 50%;
}
.centering {
  display: flex;
}
.centering-left, .centering-right {
  flex: 1;
}

.centering-right__tablepreffix {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

/*datepicker*/
.react-datepicker-wrapper, .react-datepicker__input-container, 
.react-datepicker__input-container input {
	width: 100%;
}

.table-correspondence__data {
	flex-wrap: wrap;
  word-break: break-all;
}
.table-correspondence__url {
  flex-wrap: wrap;
  word-break: break-all;
}

.dropzone-field {
  display: flex;
	border: 1px dashed #000;
	background: #359dbb;
  height: 60px;
  align-items: center;
  justify-content: center;
  font-size: 30px;
}
/* BUTTONS */
.btn-action {
  border: none;
  display: block;
  text-align: center;
  cursor: pointer;
  text-transform: uppercase;
  outline: none;
  overflow: hidden;
  position: relative;
  color: #fff;
  font-weight: 700;
  font-size: 15px;
  background-color: #8cc8ca;
  padding: 17px 60px;
  margin: 0 auto;
  box-shadow: 0 5px 15px rgba(0,0,0,0.20);
}
.btn-action span {
  position: relative; 
  z-index: 1;
}
.btn-action:after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  height: 490%;
  width: 140%;
  background: #359dbb;
  -webkit-transition: all .5s ease-in-out;
  transition: all .5s ease-in-out;
  -webkit-transform: translateX(-98%) translateY(-25%) rotate(45deg);
  transform: translateX(-98%) translateY(-25%) rotate(45deg);
}
.btn-action:hover:after {
  -webkit-transform: translateX(-9%) translateY(-25%) rotate(45deg);
  transform: translateX(-9%) translateY(-25%) rotate(45deg);
}

.btn-sort__clear svg{
  -webkit-transition: -webkit-transform .5s ease-in-out;
          transition: transform .5s ease-in-out;
}
.btn-sort__clear:hover svg{
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.svg-inline--fa.fa-w-18, .svg-inline--fa.fa-w-14 {
  width: 1em;
}
.btn-search__icon {
  display: none;
}
.btn-search__txt {
  display: inline-block;
}
.form-button {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  &__part {
    width: 49%;
  }
}

.Toastify__toast--info {
  background: #359dbb!important;
}

.flex-container {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.avatar {
    background-repeat: no-repeat;
    background-size: contain;
    background-position: 50% 50%;
    width: 150px;
    height: 150px;
    background-position: center center;
    /* centering image */
    margin: auto;
}
@media only screen and (max-width: 992px) {
  .container {
    max-width: 100%!important;
  }
}
@media only screen and (max-width: 767px) {
  .container {
    max-width: 100%!important;
  }
  .navbar-profile {
    display: block!important;
    position: fixed!important;
  }
  .table th, .table td {
    padding: 0!important;
  }
  .table-num__title, .table-num__text {
    display: none!important;
  }
  .table-investors tbody {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    max-width: 55px;
  }
  .btn-add {
    display: none!important;
  }
  .nav-logout {
    text-align: right;
  }

}

@media (max-width: 576px) {
  .container {
    max-width: 100%!important;
  }
  .table th, .table td {
    font-size: 14px!important;
  }
  .table .table-payments {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    max-width: 55px;
  }
  .table-phone__title, .table-phone__text, 
  .table-investor__title, .table-investor__text {
    display: none!important;
  }
  .btn-quick__text{
    display: none!important;
  }
  .user-username {
    margin-right: 10px;
  }
  .nav-logout {
    margin-right: 10px;
  }
}

@media (max-width: 365px) {
  .table th, .table td {
    font-size: 12px!important;
  }
  .btn-search__icon {
    display: inline-block;
  }
  .btn-search__txt {
    display: none;
  }
}